// 
export default {
  //  Vue 
  // data methods computed  ...
  // 
  // 1.    data  methods 
  // 2. mixins  
  //    
  created () {
    console.log('mixins_loginConfirm')
  },
  data () {
    return {
      title: ''
    }
  },
  methods: {

    // 
    // 1.  =>   true
    // 2.  =>     false
    loginConfirm () {
      //  token 

      if (!this.$store.getters.token) {
        // 
        // this.$dialog.confirm({
        this.$dialog.confirm({
          title: '',
          message: '',
          confirmButtonText: '',
          cancelButtonText: ''
        }).then(() => {
          // on confirm
          // 
          this.$router.replace({
            path: '/login',
            query: {
              backUrl: this.$route.fullPath // 
            }
          })
        }).catch(() => {
          // on cancel
        })
        return true
      }
      return false
    }
  }
}
