<template>
  <div class="login">
    <van-nav-bar title="" left-arrow @click-left="$router.go(-1)" />

    <div class="container">
      <div class="title">
        <h3></h3>
        <p></p>
      </div>

      <div class="form">
        <div class="form-item">
          <input v-model="mobile" class="inp" maxlength="11" placeholder="" type="text">
        </div>
        <div class="form-item">
          <input v-model="picCode" class="inp" maxlength="5" placeholder="" type="text">
          <img v-if="picUrl" :src="picUrl" @click="getPicCode" alt="">
          <!-- <img src='@/assets/code.png' @click="getPicCode" alt=""> -->
        </div>
        <div class="form-item">
          <input v-model="msgCode" class="inp" placeholder="" type="text">
          <button @click="getCode">
            {{ second === totalSecond ? '' : second + ''}}
          </button>
        </div>
      </div>

      <div @click="login" class="login-btn"></div>
    </div>
  </div>
</template>

<script>
// import { getPicCode } from '@/api/login'
import { codeLogin, getMsgCode, getPicCode } from '@/api/login'
import { Toast } from 'vant'
export default {
  name: 'LoginPage',
  data () {
    return {
      picKey: '', // 
      picUrl: '', // 
      totalSecond: 60, // 
      second: 60, //  second--
      timer: null, //  id
      mobile: '13565455555', // 
      picCode: '2222', // 
      msgCode: '' // 
    }
  },
  async created () {
    this.getPicCode()
  },
  methods: {
    // 
    async getPicCode () {
      // const { data: { base64, key } } = await getPicCode()
      const { data: { base64, key } } = await getPicCode()
      this.picUrl = base64 // 
      this.picKey = key // 
      console.log(key)
      Toast('')
      // this.$toast('')
      // this.$toast.success('')
    },

    //     
    // true
    // false
    validFn () {
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        // this.$toast('')
        Toast('')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        Toast('')
        // this.$toast('')
        return false
      }
      return true
    },

    // 
    async getCode () {
      if (!this.validFn()) {
        // 
        return
      }

      //  totalSecond  second  () 
      if (!this.timer && this.second === this.totalSecond) {
        // 
        // status200promiseawaitpromise
        await getMsgCode(this.picCode, this.picKey, this.mobile)

        // this.$toast('')
        Toast('')
        // 
        this.timer = setInterval(() => {
          this.second--
          if (this.second <= 0) {
            clearInterval(this.timer)
            this.timer = null //  id
            this.second = this.totalSecond // 
          }
        }, 1000)
      }
    },

    // 
    async login () {
      if (!this.validFn()) {
        return
      }

      if (!/^\d{6}$/.test(this.msgCode)) {
        // this.$toast('')
        Toast('')
        return
      }

      console.log('')

      const res = await codeLogin(this.mobile, this.msgCode)
      this.$store.commit('user/setUserInfo', res.data)
      // this.$toast('')
      Toast('')

      // 
      this.$store.dispatch('cart/getCartListAction')
      // 
      this.$store.dispatch('user/getAddressList')

      // 
      // 1.    => 
      // 2.  => 
      const url = this.$route.query.backUrl || '/'
      this.$router.replace(url)
    }
  },
  // 
  destroyed () {
    clearInterval(this.timer)
  }
}
</script>

<style lang="less" scoped>
.container{
  padding: 50px 40px;
  .title{
    margin-bottom: 20px;
    h3{
      font-size: 30px;
      font-weight: normal;
    }
    p{
      line-height: 40px;
      font-size: 15px;
      color: #636262;
    }
  }
  .form-item{
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }
    .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg,#ecb53c,#ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0,0,0,.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

</style>
